import { i18n } from 'i18next';
import React, { CSSProperties, useEffect, useRef, useState } from 'react';
import { WidgetWrapper } from './WidgetWrapper';
import CloseButton from './components/CloseButton';
import InstallIcon from './components/InstallIcon';
import Logo from './components/Logo';
import { FOX_IMAGE } from './constants';
import styles from './styles';
import SDKVersion from './components/SDKVersion';
import Flame from './components/Flame';
import AdvantagesListItem from './components/AdvantagesListItem';
import HeartIcon from './components/HeartIcon';
import WalletIcon from './components/LockIcon';
import LockIcon from './components/WalletIcon';



export interface InstallModalProps {
  onClose: () => void;
  link: string;
  sdkVersion?: string;
  preferDesktop: boolean;
  sentienceInstaller: {
    startDesktopOnboarding: () => void;
  };
  i18nInstance: i18n;
}



export const InstallModal = (props: InstallModalProps) => {
  const [tab, setTab] = useState<number>(props.preferDesktop ? 1 : 2);
  const qrCodeContainer = useRef<HTMLDivElement>(null);
  const { sdkVersion } = props;

  const t = props.i18nInstance.t;

  useEffect(() => {
    if (qrCodeContainer.current) {
      const QRCodeStyling = require('qr-code-styling');
      const qrCode = new QRCodeStyling({
        width: 270,
        height: 270,
        type: 'svg',
        data: props.link,
        image: FOX_IMAGE,
        dotsOptions: {
          color: 'black',
          type: 'rounded',
        },
        imageOptions: {
          margin: 5,
        },
        cornersDotOptions: {
          color: '#9A2F5B',
        },
        qrOptions: {
          errorCorrectionLevel: 'M',
        },
      });
      qrCode.append(qrCodeContainer.current);
    }
  }, [qrCodeContainer]);

  const handleSentienceDownload = () => {
    const SENTIENCE_CHROME_LINK = "https://chromewebstore.google.com/detail/sentience/ebabneklcmphnefbajcfjolfccngmmdm?hl=en";
    window.open(SENTIENCE_CHROME_LINK, '_blank');
  };

  const customSentienceInstaller = {
    ...props.sentienceInstaller,
    startDesktopOnboarding: handleSentienceDownload,
  };

  return (
    <WidgetWrapper className="install-model">
      <div style={styles.backdrop} onClick={props.onClose}></div>
      <div style={styles.modal}>
        <div style={styles.closeButtonContainer}>
          <div style={styles.right}>
            <span style={styles.closeButton} onClick={props.onClose}>
              <CloseButton />
            </span>
          </div>
        </div>
        <div style={styles.logoContainer}>
            <Logo />
        </div>
        <div>

          <div style={styles.flexContainerTabs}>
            <div style={styles.flexContainerTabsMain}>
              <div
                onClick={() => setTab(1)}
                style={{
                  ...styles.tab,
                  ...(tab === 1 ? styles.tabactive : {}),
                  ...styles.flexItemTab,
                } as CSSProperties}
              >
                {t('DESKTOP')}
                {tab === 1 && <Flame />}
              </div>
              <div
                onClick={() => setTab(2)}
                style={{
                  ...styles.tab,
                  ...(tab === 2 ? styles.tabactive : {}),
                  ...styles.flexItemTab,
                } as CSSProperties}
              >
                {t('MOBILE')}
                {tab === 2 && <Flame />}
              </div>
            </div>
          </div>

          <div style={{ display: tab === 1 ? 'none' : 'block' }}>
            <div style={styles.flexContainer}>
              <div
                style={{
                  textAlign: 'center',
                  marginTop: 4,
                  ...styles.flexItem,
                }}
              >
                <div style={styles.connectMobileDiv}>
                  <span>
                    <b>Sign in with Sentience </b>
                  </span>
                  <br />
                  Take control of your crypto and explore the blockchain with
                  the wallet trusted by over 30 million people worldwide
                </div>

                <div ref={qrCodeContainer} style={styles.center} />
                <div style={styles.connectMobileText}>
                  {t('SCAN_TO_CONNECT')} <br />
                  <span style={styles.blue}>
                    <b>{t('META_MASK_MOBILE_APP')}</b>
                  </span>
                </div>
              </div>
            </div>
          </div>

          <div style={{ display: tab === 2 ? 'none' : 'block' }}>


            <div style={styles.item}>
              <AdvantagesListItem
                Icon={HeartIcon}
                text={t('INSTALL_MODAL.TRUSTED_BY_USERS')}
              />
            </div>
            <div style={styles.item}>
              <AdvantagesListItem
                Icon={WalletIcon}
                text={t('INSTALL_MODAL.LEADING_CRYPTO_WALLET')}
              />
            </div>
            <div style={styles.item}>
              <AdvantagesListItem
                Icon={LockIcon}
                text={t('INSTALL_MODAL.CONTROL_DIGITAL_INTERACTIONS')}
              />
            </div>


            <button
              style={styles.button}
              onClick={customSentienceInstaller.startDesktopOnboarding}
            >
              <InstallIcon />
              <span style={styles.installExtensionText}>
                {t('INSTALL_MODAL.INSTALL_META_MASK_EXTENSION')}
              </span>
            </button>
          </div>
        </div>
        <SDKVersion version={sdkVersion} />
      </div>
    </WidgetWrapper>
  );
};
